
import './styles/output.css'
import { useState, useEffect } from 'react';
import ImageCard from './components/ImageCard';
import ImageSearch from './components/ImageSearch';

function App() {
  const [images, setImages] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [term, setTerm] = useState('dog')

  useEffect(()=>{
    fetch(`https://pixabay.com/api/?key=${process.env.REACT_APP_PIXABABY_API_KEY}&q=${term}&image_type=photo&pretty=true`)
    .then(res=>res.json())
    .then(data=>{
      setImages(data.hits)
      setIsLoading(false)
    })
    .catch(err=>console.log('Err :', err))
  },[term])
  console.log(term);

  return (
    
    <div className="container mx-auto">
      <ImageSearch setSearch={text=>setTerm(text)}/>
      {isLoading? <h1 className="text-6xl mx-auto text-center mt-32">Loading...</h1>:
      <div className="grid grid-cols-3 gap-4">
        {images.map(image=>(<ImageCard key={image.id} image={image} setTerm={setTerm}/>))}
      </div>}
    </div>
  );
}

export default App;
